<template>

  <b-card :header="$t('Stores')">
    <b-button type="button" style="background-color: #ef402b;border-color: #C33421;color: white" @click="showmag=false,showLoader=false,clearObjects(),getLanguage(),saveStore.IsActive=false,$refs.addStoreModel.show()" class="mr-1 mb-2 mt-xl-n2 pull-right" v-if="loginUser.Role=='SA'" >{{$t('add_store')}}</b-button>
   
    <b-table  :dark="dark" :sort-by.sync="sortBy" :hover="hover" :striped="striped"  :bordered="bordered" :small="small" :fixed="fixed" responsive="lg" :items="gridData" :fields="captions" :current-page="currentPage" :per-page="perPage">  
    
    <template slot="top-row" slot-scope="{ fields }"  >
    <td v-for="field in fields" :key="field.key"  >
      <input v-model="filters[field.key]" :placeholder="' '+field.label" @click="currentPage=1"  class="form-control"  v-if="field.key!='actions' && field.key!='IsNonPhysicalStore' && field.key!='IsActive'" >
    </td>
  </template>
  

     
      <template slot="actions" slot-scope="data">
          <a v-on:click="showLoader=false,clearObjects(),selectObject(data.item), $refs.ViewStoreModel.show(),getLanguage()" v-b-tooltip.hover :title="$t('View')" class="icon mr-2 anchor-pointer" v-if="loginUser.Role!='SA'"><i id="editIcon" class="fa font fa fa-eye"></i></a>
          <a v-on:click="showLoader=false,clearObjects(),selectObject(data.item), $refs.updateStoreModel.show(),getLanguage()" v-b-tooltip.hover :title="$t('Update')" class="icon mr-2 anchor-pointer" v-if="loginUser.Role=='SA'"><i id="editIcon" class="fa font-2x2 fa-pencil"></i></a>
          <a v-on:click="enableUser=true,clearObjects(),selectObject(data.item),$refs.disableStoreModel.show()" v-b-tooltip.hover :title="$t('Enable_Store')"  class="icon mr-2 anchor-pointer" v-if="data.item.IsActive=='Inactive'"  v-show="loginUser.Role=='SA'"><i class="fa fa-check-circle font-2x2 icons mt-1"></i></a>
          <a v-on:click="disableUser=true,clearObjects(),selectObject(data.item),$refs.disableStoreModel.show()" v-b-tooltip.hover :title="$t('Disable_Store')"  class="icon mr-2 anchor-pointer" v-if="data.item.IsActive=='Active'" v-show="loginUser.Role=='SA'"><i class="fa fa-ban font-2x2 icons mt-1"></i></a>
          <!-- <a v-on:click="clearObjects(),selectObject(data.item), $refs.deleteStoreModel.show()" v-b-tooltip.hover :title="$t('Delete')"  class="icon anchor-pointer"  v-show="loginUser.Role=='SA'"><i class="cui-trash font-2x2 icons mt-1"></i></a> -->
      </template>
    </b-table>

    <div class="pt-1 pb-4" v-if="gridData2.length == 0">
      <!-- <b-button style="background-color: #e4e5e6" class="w-100">{{$t('No Records Found.')}}</b-button> -->
      <div class="text-center">
  <b-spinner variant="primary" label="Text Centered"></b-spinner>
       </div>
    </div>

    <nav>
      <b-row>
        <b-col lg="4" sm="4" md="4">
        </b-col>
        <b-col lg="8" sm="8" md="8">
          <b-pagination class="pull-right" :total-rows="totalRows" :per-page="perPage" v-model="currentPage" prev-text="Prev" next-text="Next" hide-goto-end-buttons/>
        </b-col> 
      </b-row>
    </nav>

    <b-modal no-close-on-backdrop ref="deleteStoreModel" :title="$t('Delete_Store')" class="modal-primary">
      <p>{{$t('Are_you_sure_you_want_to_delete_store')}} <b style="color: #ef402b">{{deleteStore.StoreName}}</b> ?</p>
      <div slot="modal-footer" class="w-100">
        <b-button type="button" v-on:click="$refs.deleteStoreModel.hide()" class="float-right ml-2 "><i class="fa fa-close"></i> {{$t('Cancel')}}</b-button>
        <b-button type="button" v-on:click="deleteItem($refs.deleteStoreModel, deleteStore)"  style="background-color: #ef402b;border-color: #C33421;color: white" class="float-right ml-2 " variant="primary"><i class="fa fa-dot-circle-o"></i> {{$t('Confirm')}}</b-button>
      </div>
    </b-modal>
    <b-modal no-close-on-backdrop ref="disableStoreModel" :title="$t('Disable_Store')" class="modal-primary">
      <p>{{$t('Are_you_sure_you_want_to_Enable_or_disable_store')}} <b style="color: #ef402b">{{disableStore.StoreName}}</b> ?</p>
      <div slot="modal-footer" class="w-100">
        <b-button type="button" v-on:click="$refs.disableStoreModel.hide()" class="float-right ml-2 "><i class="fa fa-close"></i> {{$t('Cancel')}}</b-button>
        <b-button type="button" v-on:click="disableItemStore($refs.disableStoreModel, disableStore)"  style="background-color: #ef402b;border-color: #C33421;color: white" class="float-right ml-2 " variant="primary"><i class="fa fa-dot-circle-o"></i> {{$t('Confirm')}}</b-button>
      </div>
    </b-modal>

    <b-modal no-close-on-backdrop ref="toEnableStoreModel" :title="$t('Enable_Store')" class="modal-primary">
      <p>{{$t('After Saved Store Please, Go to backOffice User and assign Trader to Store for Enable')}} <b style="color: #ef402b">{{disableStore.StoreName}}</b></p>
      <div slot="modal-footer" class="w-100">
        <b-button type="button" v-on:click="$refs.toEnableStoreModel.hide()"  style="background-color: #ef402b;border-color: #C33421;color: white" class="float-right ml-2 " variant="primary"><i class="fa fa-dot-circle-o"></i> {{$t('OK')}}</b-button>
      </div>
    </b-modal>

    <b-modal id="storeModal" no-close-on-backdrop ref="addStoreModel" size="lg" hide-footer :title="$t('add_store')" class="modal-primary">
        <b-tabs pills v-model="tabIndex" class="mt-sm-1 line-height: 0.5; my-tab">
          <b-tab :title="$t('Store_Info')">
            <b-form validated id="storeInfo">
              <b-row>
                <b-col md="3">
                  <b-form-group
                    :label="$t('Physical_Store')">
                  </b-form-group>
                </b-col>
                <b-col md="3">
                  <b-form-group>
                    <!-- <toggle-button :height="30" :value="saveStore.IsNonPhysicalStore" @change="selectAllDepartment(saveStore)" v-model="saveStore.IsNonPhysicalStore"/> -->
                   <!-- <b-checkbox  value="true" :unchecked-value="null" :height="30"  @change="selectAllDepartment(saveStore)" v-model="saveStore.IsNonPhysicalStore"/> -->
                    <label class="switch">
               <input type="checkbox" value="true" unchecked-value="false"   @change="selectAllDepartment(saveStore)" v-model="saveStore.IsNonPhysicalStore" >
               <span class="slider round"></span>    
               </label>

                  </b-form-group>
                </b-col>
                <b-col md="6">
                  <b-form-group
                    :label="$t('Select_Department')" validated>
                    <b-form-select v-model="saveStore.DepartmentID" required :disabled="!saveStore.IsNonPhysicalStore" >
                        <option :value="null" >{{$t('Please_select_department')}}</option>
                        <option v-if="!saveStore.IsNonPhysicalStore" value="all">All Departments</option>
                        <option v-for="item in departmentList" :value="item.DepartmentID" v-bind:key="item.DepartmentID">{{item.DepartmentName}}</option>
                    </b-form-select>
                  </b-form-group>
                </b-col>
              </b-row>
              <b-row>
              <b-col md="6">
                <b-form-group validated
                  :label="$t('Store_Name')">
                  <b-form-input v-model="saveStore.StoreName"  type="text" :placeholder="$t('Please_enter_store_name')" required ></b-form-input>
                </b-form-group>
              </b-col>
              <b-col md="6">
                  <b-form-group validated
                  :label="$t('Category_Type')">
                  <b-input-group>
                    <b-form-select v-model="saveStore.CategoryType" required>
                     <option :value="null" disabled>{{$t('Please_Select_Category_Type')}}</option> 
                     <option value="A">{{$t('A')}}</option> 
                     <option value="B">{{$t('B')}}</option> 
                    </b-form-select>
                  </b-input-group>
                </b-form-group>
              </b-col>
            </b-row>
            <b-row>
              <b-col md="6">
                <b-form-group validated
                  :label="$t('Store_logo')">
                  <b-form-file
                    v-model="saveStore.StoreLogo"
                    :state="Boolean(saveStore.StoreLogo)"
                    :v-on:selected="checkFileSize()"
                    :placeholder="$t('Please_Select_Store_Logo')"
                    :drop-placeholder="$t('Drop_file_here')"
                    accept="video/*|image/*" required
                  />
                </b-form-group>
              </b-col>
              <b-col md="6">
                <b-form-group validated
                  :label="$t('Background_photo/video')">
                  <b-form-file
                    v-model="saveStore.BackgroundPhoto"
                    :state="Boolean(saveStore.BackgroundPhoto)"
                    :placeholder="$t('Please_Select_Background_Photo')"
                    :drop-placeholder="$t('Drop_file_here')"
                    accept="video/*|image/*"  required
                  />
                </b-form-group>
              </b-col>
            </b-row>
            <b-row>
              <b-col md="6">
                <b-form-group validated
                  :label="$t('Delivery_price')">
                  <b-form-input v-model="saveStore.DeliveryPrice" type="number" :placeholder="$t('Please_enter_Delivery_Price')"  required ></b-form-input>
                </b-form-group>
              </b-col>
              <b-col md="6">
                <b-form-group validated
                  :label="$t('Minimum_delivery')">
                  <b-form-input v-model="saveStore.MinimumDelivery" type="number" :placeholder="$t('Please_enter_Minimum_Delivery')"  required ></b-form-input>
                </b-form-group>
              </b-col>
            </b-row>
            <b-row>
              <!-- <b-col md="6">
                <b-form-group validated
                  :label="$t('Shipping_Cost')">
                  <b-form-input v-model="saveStore.ShippingCost" type="number" :placeholder="$t('Please_Enter_Shipping_Cost')"  required ></b-form-input>
                </b-form-group>
              </b-col> -->
              <b-col md="6">
                <b-form-group validated
                  :label="$t('Store_URL')">
                  <b-form-input v-model="saveStore.StoreLink" type="text" :placeholder="$t('Please_Enter_Store_URL')"  required ></b-form-input>
                </b-form-group>
              </b-col> 
              <b-col md="6">
                <b-form-group validated
                  :label="$t('Phone_Number')">
                  <b-input-group>
                    <!-- <b-form-select v-model="saveStore.countryCode" class="countryCode">
                      <option :value="null">{{$t('Select Country Code')}}</option>
                      <option ></option>
                    </b-form-select> -->
                    <b-form-input v-model="saveStore.TelephoneNumber" type="text" pattern="[1-9]{1}[0-9]{9}" :placeholder="$t('Please_enter_mobile_number')" min="10" max="10" required title="Mobile Number Should be 10 Digit" ></b-form-input>
                  </b-input-group>
                </b-form-group>
              </b-col>
            </b-row>
            <b-row>
              <b-col md="6">
                <b-form-group validated
                  :label="$t('Description')">
                  <vue-editor useCustomImageHandler class="meeting-notes-popup" v-model="saveStore.Description" required></vue-editor>
                  <!-- <b-form-textarea v-model="saveStore.Description" type="text" :placeholder="$t('Please_enter_store_description')"  required ></b-form-textarea> -->
                </b-form-group>
              </b-col>
              <b-col md="6">
                <b-form-group validated
                  :label="$t('Presentation')">
                  <vue-editor useCustomImageHandler class="meeting-notes-popup" v-model="saveStore.Presentation" required></vue-editor>
                  <!-- <b-form-textarea v-model="saveStore.Presentation" :placeholder="$t('Please_enter_store_presentation')"  required ></b-form-textarea> -->
                </b-form-group>
              </b-col>
            </b-row>
            <b-row>
              <b-col md="3">
                <b-form-group
                  :label="$t('Show_On_Homepage')">
                </b-form-group>
              </b-col>
              <b-col md="3">
                <b-form-group>
                  <!-- <toggle-button :height="30"     v-model="saveStore.ShowOnHomepage" :value="saveStore.ShowOnHomepage"/> -->
                  <!-- <b-checkbox  value="true" :unchecked-value="false" :height="50"    v-model="saveStore.ShowOnHomepage"/> -->

                    <label class="switch">
               <input type="checkbox" value="true" unchecked-value="false"   v-model="saveStore.ShowOnHomepage"  >
               <span class="slider round"></span>    
               </label>

                </b-form-group>
              </b-col>
              <b-col md="2">
                <b-form-group
                :label="$t('Store_Status')">
                </b-form-group>
              </b-col>
              <b-col md="4">
                <b-form-group>
                  <!-- <toggle-button :height="30"   v-model="saveStore.IsActive" :value="saveStore.IsActive"/> -->
                  <!-- <b-checkbox  value="true" :unchecked-value="false" :height="50"    v-model="saveStore.IsActive"/> -->
                <label class="switch">
               <input type="checkbox" value="true" unchecked-value="false"   v-model="saveStore.IsActive" v-on:click="$refs.toEnableStoreModel.show(),showmag=true" onclick="return false;" >
               <span class="slider round"></span>    
               </label>
                
                </b-form-group>
               
              </b-col>
               <div   style="padding: 0 0 0 16px;">
                 <label style="font-size: 14px; font-weight: 900; color:red">
                 {{$t('After_Saved_Store_Please_Go_to_backOffice_User_and_assign_Trader_to_Store_for_Enable_store')}} 
                 </label>
                 </div>   
            
            </b-row>
            <b-card-footer class="w-100 text-right">
                <div slot="footer">
                <b-button type="button" variant="secondary" @click="forTab1 ($refs.addStoreModel)" class="ml-2" style="background-color: #ef402b;border-color: #C33421;color: white" ><i class="fa fa-dot-circle-o"></i> {{$t('Next')}}</b-button>
                <b-button type="button" class="ml-2" @click="tabIndex=0" v-on:click="$refs.addStoreModel.hide(),clearObjects()" ><i class="fa fa-close"></i> {{$t('Cancel')}}</b-button>
                </div>
            </b-card-footer>
            </b-form>
        </b-tab>
        <b-tab :title="$t('Store_Location')">
          <b-form validated id="address">
              <b-row>
              <b-col md="12">
                <b-form-group validated
                  :label="$t('Principal_Address')">
                </b-form-group>
                <hr style="margin-top: -1rem !important;" />
              </b-col>
            </b-row>
            <b-row v-for="(saveStore, index) in otherAddr" :key="index">
              <b-col md="3">
                <b-form-group validated
                  :label="$t('Street_name')">
                  <b-form-input id="streetName"  v-model="saveStore.StreetName" type="text"  :placeholder="$t('Street_name')" required ></b-form-input>
                </b-form-group>
              </b-col>
              <b-col md="2">
                <b-form-group validated
                  :label="$t('City')">
                  <b-form-input type="text"  v-model="saveStore.City" :placeholder="$t('City')"  v-on:change="handleEnter('P', saveStore, $event,index)" required ></b-form-input>
                </b-form-group>
              </b-col>
              <b-col md="2">
                <b-form-group validated
                  :label="$t('Postal_Code')">
                  <b-form-input type="text" v-on:click="handleEnter('P', saveStore, $event,index)" v-model="saveStore.PostalCode" :placeholder="$t('Postal_Code')" required ></b-form-input>
                </b-form-group>
              </b-col>
              <b-col md="2">
                <b-form-group validated
                  :label="$t('lat')">
                  <b-form-input type="text" v-model="saveStore.Lat" :placeholder="$t('lat')" required ></b-form-input>
                </b-form-group>
              </b-col>
              <b-col md="2">
                <b-form-group validated
                  :label="$t('lng')">
                  <b-form-input type="text" v-model="saveStore.Lng" :placeholder="$t('lng')" required ></b-form-input>
                </b-form-group>
              </b-col>
              <b-col md="1" v-if="index > 0">
                <b-button style="margin: 25px 0px 0px 0px;" @click="removeOtherAddr(index)" type="button" class="mb-2 ml-2" variant="secondary" v-b-tooltip.hover :title="$t('Remove')"><i class="cui-trash font-2x2 icons mt-1"></i></b-button>
              </b-col>
              <b-col md="12" v-if="index == 0">
                <b-row >
                  <b-col md="11">
                    <b-form-group style="margin-bottom: -10px"
                      :label="$t('Other_Address')">
                    </b-form-group>
                  <hr style="margin-top: 0.5rem !important;" />
                  </b-col>
                  <!-- <b-col md="1">
                    <b-form-group>
                      <b-button type="button" style="background-color: #ef402b;border-color: #C33421;color: white" class="ml-2 text-left" v-b-tooltip.hover :title="$t('Add Power Transfer')" @click="addOtherAddr"><b>+</b></b-button>
                    </b-form-group>
                  </b-col> -->
                </b-row>                
              </b-col>
            </b-row>
            
            <b-row>
              <b-col md="12">
                <gmap-map
                  id="map"
                  :center="center"
                  :zoom="5"
                  :clickable="true"
                  map-type-id="terrain"
                  style="width: 500px; height: 300px"
                  >
                   <GmapMarker
                      :key="index"
                      v-for="(m, index) in markers"
                      :position="m.position"
                      :clickable="true"
                      :draggable="true"
                      @click="center=m.position"
                    />

                </gmap-map>
              </b-col>
            </b-row>
          <b-card-footer class="w-100 text-right">
              <div slot="footer text-right w-100">
              <b-button type="button" variant="secondary" class="ml-2" @click="tabIndex=0" ><i class="fa fa-step-backward"></i>  {{$t('Previous')}}</b-button>
              <b-button type="button" variant="secondary" class="ml-2" @click="forTab2 ($refs.addStoreModel)" ><i class="fa fa-step-forward"></i> {{$t('Next')}}</b-button>
              <b-button type="button" class="ml-2" v-on:click="$refs.addStoreModel.hide(),clearObjects()" ><i class="fa fa-close"></i> {{$t('Cancel')}}</b-button>
              </div>
          </b-card-footer>
          </b-form>
        </b-tab>
        
          <b-tab :title="$t('Product_Types')">
            <b-form validated id="productTypes">
              <b-row>
                <b-col md="12">
                  <b-row >
                    <b-col md="8">
                      <b-form-group validated
                        :label="$t('Types_Of_Products')">
                      </b-form-group>
                    </b-col>
                    <b-col md="2">
                      <b-form-group v-if="!saveStore.selectAll"
                        :label="$t('Select_All')">
                      </b-form-group>
                      <b-form-group v-if="saveStore.selectAll"
                        :label="$t('Unselect_All')">
                      </b-form-group>
                    </b-col>
                    <b-col md="2">
                      <b-form-group>
                        <toggle-button  :height="30" @change="selectUnselectAll(StoreCategoryID1, saveStore.selectAll)" v-model="saveStore.selectAll"/>
                      </b-form-group>
                    </b-col>
                  </b-row>
                  <hr style="margin-top: -1rem !important;" />
                </b-col>
              </b-row>
              <b-row id="productTypes" >
                <b-col md="3" v-for="productType in productTypeList" :key="productType.StoreCategoryID">
                  <b-form-group>
                    <b-checkbox :value="productType.StoreCategoryID" v-model="StoreCategoryID1"  @change="selectPrductType($event,productType.StoreCategoryID)" >{{language=="fr"?productType.StoreCategoryNameFrench:productType.StoreCategoryName}}</b-checkbox>
                  </b-form-group>
                </b-col>
                 
              </b-row>
            <b-card-footer class="w-100 text-right">
                <div slot="footer text-right w-100">
                <b-button type="button" variant="secondary" class="ml-2" @click="tabIndex=1" ><i class="fa fa-step-backward"></i>  {{$t('Previous')}}</b-button>
                <b-button type="button" class="ml-2" v-if="showLoader==false"   v-on:click="forTab3 ($refs.addStoreModel),saveItem($refs.addStoreModel, saveStore),$refs.toEnableStoreModel.show();" style="background-color: #ef402b;border-color: #C33421;color: white" ><i class="fa fa-dot-circle-o"></i> {{$t('Save')}}</b-button>
                  &nbsp;&nbsp;
                <b-button variant="primary" v-if="showLoader==true">
                    <b-spinner small></b-spinner>
                    <span class="sr-only">Loading...</span>
                  </b-button> 
                <b-button type="button" class="ml-2" @click="tabIndex=0"  v-on:click="$refs.addStoreModel.hide(),clearObjects()" ><i class="fa fa-close"></i> {{$t('Cancel')}}</b-button>
                </div>
            </b-card-footer>
            </b-form>
          </b-tab>
    


        </b-tabs>
      </b-modal>













































<b-modal id="editModal" no-close-on-backdrop ref="updateStoreModel" size="lg" hide-footer :title="$t('Edit_Store')" class="modal-primary">
        <b-tabs pills v-model="edittabIndex" class="mt-1 my-tab">
          <b-tab :title="$t('Store_Info')">
            <b-form validated id="storeInfo">
              <b-row>
                <b-col md="3">
                  <b-form-group
                    :label="$t('Physical_Store')">
                  </b-form-group> 
                </b-col>
                <b-col md="3">
                  <b-form-group> 
                   <!-- <toggle-button :height="30" :sync="editStore.IsNonPhysicalStore"  @change="selectAllDepartment(editStore)" v-model="editStore.IsNonPhysicalStore"/> -->
                    <!-- <b-checkbox  :height="50"  @change ="selectAllDepartment(editStore)" v-model="editStore.IsNonPhysicalStore"/> -->
                <label class="switch">
               <input type="checkbox" value="true" unchecked-value="false"   @change ="selectAllDepartment(editStore)" v-model="editStore.IsNonPhysicalStore" >
               <span class="slider round"></span>    
               </label>
                    <!-- <toggle-button :height="30" :sync="editStore.IsNonPhysicalStore"  @change="selectAllDepartment(editStore)" v-model="editStore.IsNonPhysicalStore"/> -->
                  </b-form-group>
                </b-col>
                <!-- <b-col md="3">
                  <b-form-group> {{editStore.IsNonPhysicalStore}}
                    <toggle-button :height="30" :value="editStore.IsNonPhysicalStore" :v-model="editStore.IsNonPhysicalStore" @change="selectAllDepartment(editStore)"  />
                  </b-form-group>
                </b-col> -->
               <!-- <b-col md="3">
                  <b-form-group>=={{editStore.IsNonPhysicalStore}}
                    <toggle-button :height="30" :value="getCheckedToggle(editStore.IsNonPhysicalStore)" @change="selectAllDepartment(editStore)"  />
                  </b-form-group>
                </b-col>-->
                <b-col md="6">
                  <b-form-group
                    :label="$t('Select_Department')" validated>
                    <!-- <b-form-select v-model="editStore.DepartmentID" required > -->
                     <b-form-select v-model="editStore.DepartmentID"  :disabled="!editStore.IsNonPhysicalStore" > 
                        <option :value="null" disabled>{{$t('Please_select_department')}}</option>
                        <option v-if="editStore.IsNonPhysicalStore" value="all">All Departments</option>
                        <option v-for="item in departmentList" :value="item.DepartmentID" v-bind:key="item.DepartmentID">{{item.DepartmentName}}</option>
                    </b-form-select>
                  </b-form-group>
                </b-col>
              </b-row>
              <b-row>
              <b-col md="6">
                <b-form-group validated
                  :label="$t('Store_Name')">
                  <b-form-input v-model="editStore.StoreName" type="text" :placeholder="$t('Please_enter_store_name')" required ></b-form-input>
                </b-form-group>
              </b-col>
              <b-col md="6">
                  <b-form-group validated
                  :label="$t('Category_Type')">
                  <b-input-group>
                      
                     <b-form-select v-model="editStore.CategoryType" required>
                     <option :value="null" disabled>{{$t('Please_Select_Category_Type')}}</option> 
