    },
    
    checkFileSize:function (){
    
    //const file = this.saveStore.StoreLogo;
      
    // if (this.saveStore.StoreLogo.size > 1024 * 1024) {
    //     e.preventDefault();
    //     alert('File too big (> 1MB)');
    //     return;
    //   }
    //   alert('File  is OK');
    },  

     gridDataRefresh : function() {
       var filtered = this.storesList.filter(item => {
        return Object.keys(this.filters).every(key =>
            // String(item[key]).includes(this.filters[key]))
            String(item[key]).toLowerCase().includes( String(this.filters[key]).toLowerCase()))
      })
      return filtered.length > 0 ? filtered : [{
        StoreName: '',
      TelephoneNumber: ''
      }]
