    selectObject: function(store){
    
      // console.log("selected_store :",store);
      // console.log("this.otherAddr",this.otherAddr);

      this.editStore.StoreID=store.StoreID;
      this.editStore.StoreName=store.StoreName; 
      this.editStore.Presentation=store.Presentation;
      this.editStore.Description=store.Description;
      this.editStore.BackgroundPhoto=store.BackgroundPhoto;
      this.editStore.BackgroundPhoto_preview=toutPaieService.ApiEndPoints+"images/"+store.BackgroundPhoto;
      this.editStore.StoreLogo=store.StoreLogo;
      this.editStore.StoreLogo_preview=toutPaieService.ApiEndPoints+"images/"+store.StoreLogo;
      this.editStore.TelephoneNumber=store.TelephoneNumber;
      this.editStore.DeliveryPrice=store.DeliveryPrice;
      this.editStore.MinimumDelivery=store.MinimumDelivery;
      this.editStore.CategoryType=store.CategoryType;
      this.editStore.ShippingCost=store.ShippingCost;
      this.editStore.IsActive= store.IsActive == "Active"||store.IsActive=="Y"?true:false;
      this.editStore.IsNonPhysicalStore= store.IsNonPhysicalStore == "Yes" || store.IsNonPhysicalStore=="Y" || store.IsNonPhysicalStore=="true" || store.IsNonPhysicalStore==true ? true : false;
      this.editStore.ShowOnHomepage= store.ShowOnHomepage == "Yes"||store.ShowOnHomepage=="Y"?true:false;
      this.editStore.StoreLink=store.StoreLink;
      this.editStore.Lat=store.Lat;
      this.editStore.Lng=store.Lng;
      if(store.storeAddress.length>0) {
      this.otherAddr=store.storeAddress;
      this.editStore.Addresses=store.storeAddress;
     
      }
   // console.log("this.otherAddr ::",this.otherAddr);
