      //     };
      //     this.markers.push( this.newMarker);
      //   }
        
      // }
      
      for(var i=0;i<this.otherAddr.length;i++){
      this.handleEnter('P',this.otherAddr[i],null,null); 
      }
    // console.log(this.markers);
     
      if(store.storeDepartment.length>0)
      {
       this.editStore.DepartmentID=store.storeDepartment[0].DepartmentID;
      }else{
        this.editStore.DepartmentID=null;
      }
      
      for(var  i=0;i<store.storeProductsTypes.length;i++) {
        this.StoreCategoryID1.push(store.storeProductsTypes[i].StoreCategoryID);
      }
       
      this.editStore.StoreCategoryID=this.StoreCategoryID1;
        
        // this.handleEnter('P', this.editStore, null);
      this.deleteStore=store;
      this.disableStore=store;
     // console.log("selectObject to update :", JSON.stringify(this.editStore));
    },
     
    disableItemStore: function(ref,disableStore) {
       
     // this.disableUser = true;
      // this.saveItem(ref,disableStore);
      this.disableEnableStore(ref,disableStore);
     // this.disableUser = false;
    },
     

    deleteItem: function (refs, deleteStore) {
    //  console.log('in delete function');

      let URL = 'store/delete/' + deleteStore.StoreID;

   toutPaieService.getData(URL).then(res => {
       let msg=res.status;
       if (msg=="Success") {
          
          refs.hide();
          VueNotifications.success({message:this.translateString("Store_Deleted_Successfully"),timeout:2500});
          this.clearObjects();
          this.getStores();
          
        }else{
           VueNotifications.error({message: this.translateString(msg) ,timeout:2500});
        }

    });
    },
    
  
    updateItem: function(ref,editStore){
      //  console.log('in update function');

      console.log('in update function',editStore);
      this.editUser = true;
      this.saveItem(ref,editStore);
    },

    selectPrductType:function(event,value){
      var index =  null;
      if(!event) {
        index= this.StoreCategoryID1.indexOf(value);
        while (index > -1) {
          this.StoreCategoryID1.splice(index, 1);
          index = this.StoreCategoryID1.indexOf(value);
        }
      } else {
        // console.log("event :",event);
        this.StoreCategoryID1.push(event);
      }
     // console.log("StoreCategoryID1 :",this.StoreCategoryID1);
    },

    saveItem: function(ref,selectedStore){
      //  console.log('in save itom function');
      
      this.showLoader=true;
     //assign selected ProductType to   selectedStore Object
     selectedStore.StoreCategoryID=this.StoreCategoryID1;
  
      let userData = selectedStore;
      
      for(var i=0;i<this.otherAddr.length;i++)
      {
          if(i==0)
          {this.otherAddr[i].AddressType="P";
          }else
          {
            this.otherAddr[i].AddressType="O";
          }
      }
          userData.otherAddr=this.otherAddr;
            

      //  console.log("userData :",userData);   
      //  console.log("addr",this.otherAddr);
      var form = new FormData();
      
      form.append("BackgroundPhoto", userData.BackgroundPhoto);
      form.append("CategoryType",userData.CategoryType);
      form.append("DeliveryPrice",userData.DeliveryPrice);
      //form.append("Description",this.stripHtstripHtml(userData.Description));
      form.append("Description",userData.Description);
      form.append("MinimumDelivery",userData.MinimumDelivery);
      //form.append("Presentation",this.stripHtstripHtml(userData.Presentation));
       form.append("Presentation",userData.Presentation);
      form.append("StoreLogo",userData.StoreLogo);
      form.append("ShippingCost","0");
      form.append("StoreName",userData.StoreName);
      
      if(userData.TelephoneNumber){

        if(userData.TelephoneNumber.length=="10" ){
      form.append("TelephoneNumber",userData.TelephoneNumber);
       }else{
               this.showLoader=false;
              VueNotifications.error({message:this.translateString("Mobile_Number_Should_be_10_Digit") ,timeout:2500});
                this.tabIndex= 0;
                

       }
      }else{
               this.showLoader=false;
              VueNotifications.error({message:this.translateString("Mobile_Number_Should_be_10_Digit") ,timeout:2500});
                this.tabIndex= 0;
                

       }
      
      form.append("DepartmentID[0]",userData.DepartmentID);
      form.append("IsNonPhysicalStore",((userData.IsNonPhysicalStore==true||userData.IsNonPhysicalStore=="true")?"Y":"N"));
      form.append("StoreLink",(userData.StoreLink!=null?(userData.StoreLink).includes("http")? userData.StoreLink:("https://"+userData.StoreLink):""));
      form.append("IsActive",((userData.IsActive==true||userData.IsActive=="true")?"Y":"N"));
      form.append("ShowOnHomepage",((userData.ShowOnHomepage==true||userData.ShowOnHomepage=="true")?"Y":"N"));
      
      if(userData.otherAddr.length>0){
             if(userData.otherAddr[0]!="null"){
                for(var i=0;i<userData.otherAddr.length;i++)
                {
                form.append("Addresses["+i+"]",JSON.stringify(userData.otherAddr[i]));
                }
               }else{
                 this.showLoader=false;
                VueNotifications.error({message: this.translateString("Check_Address_Tab"),timeout:2500});
                this.tabIndex= 1;
              this.edittabIndex=0;
              }

      }


      if(this.StoreCategoryID1.length>0){
                for(var i=0;i<this.StoreCategoryID1.length;i++)
                {
                form.append("StoreCategoryID["+i+"]",JSON.stringify(this.StoreCategoryID1[i]));
                }
      }  

      if(this.disableUser){

     form.append("StoreID", userData.StoreID);
      }

      

      let url =  null;
      if (this.editUser === true && userData.StoreID) {
        //this.checkValidation (ref);
        url = 'store/update/' + userData.StoreID;
        
       
      }
      else if(this.disableUser==true&&(userData.IsActive=="Active"||userData.IsActive=="Y"||userData.IsActive==true)){
         url="store/disable/"+userData.StoreID;

      }else if(this.enableUser==true&&(userData.IsActive=="Inactive"||userData.IsActive=="N"||userData.IsActive==false)){
           url="store/enable/"+userData.StoreID;
      }
       else {
        //this.checkValidation (ref);
        url = 'store/save';
      }
      // console.log("url :",url);


     




      toutPaieService.saveData(form, url).then(data => {
        //console.log('SAVEdata', form);
        let msg=data.data[0].message;

        if (data.status == 200 && msg=="Success") {
          
          
          
          ref.hide();
          // VueNotifications.success({message:this.translateString("Store_Successfully_Saved"),timeout:2500});
          //clear StoreCategoryID1 array
          
          this.StoreCategoryID1=[];
          this.otherAddr=[];
          this.tabIndex=0;
          this.edittabIndex=0;
          this.showLoader=false;
         
                 
           this.editUser = false;
            this.disableUser=false;
          this.enableUser=false;
                

              VueNotifications.success({message:this.translateString("Store_Successfully_Saved"),timeout:2500});

             // refs.toEnableStoreModel.show();
           
          this.clearObjects();
          // console.log("after save selectedStore :",this.selectedStore);
          // console.log("after saveStoreCategoryID1 :",this.StoreCategoryID1);
          // console.log("after save editStore :",this.editStore);

           
          this.getStores();
          this.gridDataRefresh();
          
        }else{
          this.disableUser=false;
          this.enableUser=false;
          this.showLoader=false;
           VueNotifications.error({message: this.translateString(msg) ,timeout:2500});
        }
         
      });
    },



     disableEnableStore: function(ref,selectedUser) {
      
      let url =null; 
      let trader=false;

      // console.log(selectedUser);
      

      if((selectedUser.IsActive=="Active"||selectedUser.IsActive=="Y"||selectedUser.IsActive==true)){
      url="store/disable/"+selectedUser.StoreID;
      }else{
        url="store/enable/"+selectedUser.StoreID;
      }

                               for(var k=0;k<this.managersList.length;k++){
                                      
                                      if(this.managersList[k].StoreID==selectedUser.StoreID){         
                                          trader=true;
                                      }
                              }

                              
                              if(!trader){

                                 ref.hide();
                                 VueNotifications.error({message:this.translateString("Please_Assign_Trader_to_Store"),timeout:3500});
                                 return;
                              }

    toutPaieService.saveData(selectedUser, url).then(data => {
        // console.log('data', data);
        let msg=data.data[0].message;
        if (data.status == 200 && msg=="Success") { 
          ref.hide();
          this.getStores();
          if(selectedUser.IsActive=="Y" || selectedUser.IsActive=="Active"){
          VueNotifications.success({message:this.translateString("Store_Disabled_Successfully_Saved"),timeout:2500});
          // window.location.reload();
          }else{
             VueNotifications.success({message:this.translateString("Store_Enabled_Successfully_Saved"),timeout:2500});
            //  window.location.reload();
          }
          this.clearObjects();

        }else{
           VueNotifications.error({message:this.translateString(msg),timeout:2500});
        }
      });

    },

    stripHtstripHtml(html){

      console.log("edited html :",html);

    var temporalDivElement = document.createElement("div");

    temporalDivElement.innerHTML = html;
    return temporalDivElement.textContent || temporalDivElement.innerText || "";
  }, 


   

    getPhysicalStoreChanged : function(data) {
     
      data==true?this.editStore.IsNonPhysicalStore=false:this.editStore.IsNonPhysicalStore=true;
      //  console.log("data ",data);
    },

    handleEnter: function (type, addressObj,   event,index) {

    //  console.log("index =>",index);

    

      //  console.log("handleEnter :",type, addressObj,   event);
        
        // console.log("markers=>",this.markers);
        
        
        

        axios({
          url: 'https://maps.googleapis.com/maps/api/geocode/json?address='+ addressObj.StreetName+","+addressObj.City+
            '&key=AIzaSyDLsSpp4G4_P0UqeSV5upj_Wu2KkXtUK9A',
          method: 'GET'
        }).then((response) => {
          if (response.data.results && response.data.results.length > 0) {
            var location = response.data.results[0].geometry.location;


            if (type == 'P' && this.markers[index]!=null ) {
              this.markers[index] = {
                position: location,
              };
            } else {
              this.markers.push({
                position: location,
              });
            }

           // console.log("this.markers",this.markers);

            
           
            addressObj.Lat = location.lat;
            addressObj.Lng = location.lng;
            this.center = response.data.results[0].geometry.location;
           // console.log("center :",this.center);
           // console.log(response.data.results[0].geometry.location);
            response.data.results[0].address_components.find(address => {
              if (address.types.includes('postal_code')) {
                    if(address.long_name!=null){
                      addressObj.PostalCode = address.long_name;
                    }else{
                        addressObj.PostalCode = null;
                    }
                
              } else if (address.types.includes('locality') || address.types.includes('administrative_area_level_2')) {
                addressObj.City = address.long_name;
              }
            });
          }
        });
 
    },

