    getStores: function () {
      
    
      this.tabIndex= 0;
      this.edittabIndex=0;
      
     this.loginUser=JSON.parse(window.localStorage.getItem("loginUser"));
         
        let url =null;
       if(this.loginUser.Role=="M"){
           url = 'user/store/get/'+this.loginUser.UserID;
       }else{
                     url="store";
       }
       
       
      toutPaieService.getData(url).then(res => {
        this.storesList = res.data;
          for(var i=0;i<this.storesList.length;i++)
          {
            this.storesList[i].IsActive=(this.storesList[i].IsActive=="Y"?"Active":"Inactive");
            this.storesList[i].IsNonPhysicalStore=(this.storesList[i].IsNonPhysicalStore=="Y"?"Yes":"No");
            this.storesList[i].ShowOnHomepage=(this.storesList[i].ShowOnHomepage=="Y"?"Yes":"No");
          }

        // console.log("this.storesList",this.storesList);
      });

    


    }
